<template>
    <div>
        <div class="ibox">
            <div class="ibox-title">
                <div class="pull-right control">
                    <el-button size="small" @click="$refs.domainDetail.showInfoDialog()" type="info" icon="edit">添加 </el-button>
                    <el-button size="small" @click="$refs.inputDomain.showInfoDialog()" type="info" icon="edit">批量导入 </el-button>
                    <el-button size="small" @click="$refs.domainModify.showDomainDialog()" type="info" icon="edit">批量更新 </el-button>
                    <el-button size="small" :disabled="!buttonStatus"
                               @click="batchDeleteAction">批量删除
                    </el-button>
                    <a class="a-tbn" @click="exportUrl(1)" >导出微信可用域名
                    </a>
                    <a class="a-tbn" @click="exportUrl(2)" >导出qq可用域名
                    </a>
                </div>
                <h3> 收徒域名列表 </h3>
            </div>
            <div class="ibox-filter">
                <el-form :inline="true" label-position="top">
                    <el-form-item label="域名">
                        <el-input placeholder="域名"
                                  size="small"
                                  @keyup.enter.native="filterAction()"
                                  v-model.trim="filter.url">
                        </el-input>
                    </el-form-item>
                    <div class="filter-item" style="margin-top:27px;display:inline-block;">
                        <el-date-picker
                                v-model="dateRange"
                                @change="filterAction()"
                                type="daterange"
                                placeholder="选择日期范围">
                        </el-date-picker>
                    </div>

                    <el-form-item label="状态">
                        <el-select
                                size="small"
                                @change="filterAction()"
                                v-model="filter.status" >
                            <el-option
                                    v-for="(item, $key) in status.status1"
                                    :value="item.value"
                                    :label="item.name"
                                    :key="$key">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="分组">
                        <el-select
                                size="small"
                                @change="filterAction()"
                                v-model="filter.group_num" >
                            <el-option
                                    v-for="(item, $key) in status.group_num"
                                    :label="item.name"
                                    :value="item.value"
                                    :key="$key">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="用途">
                        <el-select
                                size="small"
                                @change="filterAction()"
                                v-model="filter.purpose" >
                            <el-option
                                    v-for="(item, $key) in status.purpose"
                                    :label="item.name"
                                    :value="item.value"
                                    :key="$key">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="操作">
                        <el-button size="small" @click="filterAction($event)" type="primary" icon="search">搜索</el-button>
                    </el-form-item>
                </el-form>
            </div>

            <div class="ibox-content">
                <div class="control">
                    <el-table :data="filterListDate"
                              :row-class-name="tabRowClass"
                              @selection-change="handleSelectionChange"
                              element-loading-text="拼命加载中"
                              v-loading="listData.loading"
                              style="width: 100%">
                        <el-table-column
                                type="selection"
                                width="55">
                        </el-table-column>
                        <el-table-column label="id"
                                         width="80"
                                         prop="id">
                        </el-table-column>
                        <el-table-column label="domain"
                                         width="150px"
                                         prop="url">
                        </el-table-column>
                        <el-table-column label="用途"
                                         min-width="100"
                                        >
                            <template scope="scope">
                                {{ status.purpose[scope.row.purpose].name }}
                            </template>

                        </el-table-column>
                        <el-table-column label="分组"
                                         min-width="180"
                                        >
                            <template scope="scope">
                                {{ status.group_num[scope.row.group_num].name }}
                            </template>
                        </el-table-column>
                        <el-table-column label="wx状态"
                                         width="100">
                            <template scope="scope">
                                {{ status.context[scope.row.wx] }}
                            </template>
                        </el-table-column>
                        <el-table-column label="qq状态"
                                         width="170"
                                         >
                            <template scope="scope">
                                {{ status.context[scope.row.qq] }}
                            </template>
                        </el-table-column>
                        <el-table-column label="解析状态"
                                         width="170"
                                         >
                            <template scope="scope">
                                {{ status.parsing_status[scope.row.parsing_status] }}
                            </template>
                        </el-table-column>
                        <el-table-column label="域名状态"
                                         width="170"
                                         >
                            <template scope="scope">
                            {{ status.status[scope.row.status].name }}
                            </template>
                        </el-table-column>
                        <el-table-column label="use_qq"
                                         width="170"
                                         prop="use_qq">
                        </el-table-column>
                        <el-table-column label="创建时间"
                                         width="170"
                                         prop="create_time">
                        </el-table-column>
                        <el-table-column label="更新时间"
                                         width="170"
                                         prop="update_time">
                        </el-table-column>
                        <el-table-column label="操作"
                                         width="220">
                            <template scope="scope">
                                <el-button size="small" type="info"  @click="$refs.domainDetail.showInfoDialog(scope.row)">编辑</el-button>
                                <el-button size="small" type="info" v-show="scope.row.status == 0" @click="replaceDomain(scope.row.id)">替换</el-button>
                                <el-button size="small" type="info"  @click="deleteDomain(scope.row.id)">删除</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </div>

                <div class="control">
                    <el-pagination @current-change="handleCurrentChange"
                                   :current-page="filter.page"
                                   :page-size="20"
                                   :page-count="listData.totalPage"
                                   layout="total, prev, pager, next, jumper">
                    </el-pagination>
                </div>
            </div>

            <domain-detail ref="domainDetail" @update="getList"></domain-detail>
            <domain-modify ref="domainModify" @update="getList"></domain-modify>
            <input-domain ref="inputDomain" @update="getList"></input-domain>

        </div>
    </div>
</template>

<style scope="" lang="less" type="text/scss">
    .el-table {
        tr:hover {
            td {
                background-color: initial !important;
            }
        }
        tr.group_item {
            background: rgba(0, 0 ,0 , 0.05);
        }
        tr.group_foot {
            background: #18bda5;
            color: #fff;
        }
    }
    .el-input__inner{
        height:30px!important;
    }
    .a-tbn{
        color: #fff;
        background-color: #50bfff;
        border-color: #50bfff;
        padding: 7px 9px;
        font-size: 12px;
        border-radius: 4px;
        margin-left:15px
    }
</style>

<script>
    import moment from 'moment';
    import dateFormat from '../../module/DateFormat';
    import QuRequest from '../../core/QuRequest';
    import DomainDetail from './DomainDetail.vue';
    import DomainModify from './DomainModify.vue';
    import InputDomain from './InputDomain.vue';
    import Vue from 'vue'
    import q from 'q'
    import Auth from '../../core/Auth';

    const token = Auth.getToken();

    const quRequest = new QuRequest()
    const _status = {
        group_num:[
            { 'name': '未分组', 'value': '0'},
            { 'name': '1组','value': '1'},
            { 'name': '2组', 'value': '2'},
            { 'name': '3组', 'value': '3'},
            { 'name': '4组','value': '4'},
            { 'name': '5组', 'value': '5'},
            { 'name': '6组', 'value': '6'},
            { 'name': '7组', 'value': '7'},
            { 'name': '8组','value': '8'},
            { 'name': '9组', 'value': '9'},
            { 'name': '10组', 'value': '10'},
        ],
        status:[
            { 'name': '启用','value': '0'},
            { 'name': '禁用', 'value': '1'},
            { 'name': '备用', 'value': '2'},
        ],
        status1:[
            { 'name': '全部','value': '0'},
            { 'name': '启用', 'value': '1'},
            { 'name': '备用', 'value': '2'},
            { 'name': '禁用','value': '3'},
            { 'name': '已删除', 'value': '4'},
        ],
        purpose:[
            { 'name': '默认', 'value': '0'},
            { 'name': '微信','value': '1'},
            { 'name': '朋友圈', 'value': '2'},
            { 'name': '二维码', 'value': '3'},
            { 'name': 'QQ','value': '4'},
            { 'name': '专用', 'value': '5'},
            { 'name': '微博', 'value': '6'},
            { 'name': '视频教程', 'value': '7'},
            { 'name': '唤醒徒弟','value': '8'},
            { 'name': '面对面收徒', 'value': '9'},
        ],
        context: {
            '0': '正常',
            '1': '拦截'
        },
        parsing: {
            '0': '未解析',
            '1': '已解析'
        },
        parsing_status: {
            '0': '全部',
            '1': '已解析',
            '2': '未解析'
        },
    }


    export default {
        components: {
            DomainDetail,
            InputDomain,
            DomainModify
        },
        data() {
            return {
                buttonStatus:false,
                listData: {
                    list: [],
                    totalPage: 1,
                    loading: false
                },
                status: _status,
                dateRange: '',
                filter: {
                    start_date: dateFormat(-7),
                    end_date: dateFormat(),
                    key_word: '',
                    url:'',
                    group_num:'0',
                    status: '0',
                    purpose:'0',
                    page:1,
                },
            }
        },
        mounted() {
            this.getList();
        },
        computed: {
            filterListDate() {
                return this.listData.list.map((item) => {
                    if(!item.update_time && !item.create_time) {
                        item.update_time = moment(item.update_time).format('YYYY-MM-DD hh:mm:ss');
                        item.create_time = moment(item.create_time).format('YYYY-MM-DD hh:mm:ss');
                    }
                    return item;
                });
            },
        },
        methods: {
            handleSelectionChange(val) {
                this.multipleSelection = val;
                this.buttonStatus = true;
            },
            tabRowClass(row, $index) {
                return row.group_class;
            },
            filterAction() {
                this.filter.page = 1;
                this.getList();
            },
            handleCurrentChange(page) {
                this.filter.page = page;
                this.getList();
            },
            batchDeleteAction(){
                let checks = this.multipleSelection;
                let array_list = [];
                if (checks.length < 1) {
                    this.$message.error('请勾选列表');
                } else {
                    checks.forEach((item, index) => {
                        array_list.push(this.createAllowReq(item.id));
                    });

                    q.all(array_list).then((res) => {
                        this.getList();
                    });
                }
            },
            createAllowReq(id){
                return quRequest.send(quRequest.Apis.qukan.domain.batch, {
                    ids: id,
                    action:'delete',
                }).then(() => {
                    this.$message.success("批量删除成功");
                }).catch(err => {
                    this.$message.error(`获取分类数据失败：${ err.message || '未知错误' }`);
                    throw err;
                });
            },
            deleteDomain(id){
                quRequest.send(quRequest.Apis.qukan.domain.delete,{id:id}
                ).then(res => {
                    this.getList();
                }).catch(err => {
                    this.$message.error(`获取内容列表数据失败：${ err.message || '未知错误' }`);
                }).fin(() => {
                    this.$message({
                        type: 'success',
                        message: '删除成功!'
                    })
                }).done();
            },

            exportUrl(type){
                let url = quRequest.getUrl(quRequest.Apis.qukan.domain.exportUrl);
                window.open(url + '?token=' + token + '&type=' + type, '_blank');
            },
            replaceDomain(id){
                quRequest.send(quRequest.Apis.qukan.domain.replaceDomain,{
                    id: id
                }).then(res => {
                    this.getList();
                }).catch(err => {
                    this.$message.error(`获取内容列表数据失败：${ err.message || '未知错误' }`);
                });
                this.$message({
                    type: 'success',
                    message: '替换成功!'
                });
            },
            getList(){
                quRequest.send(quRequest.Apis.qukan.domain.getList,this.filter
                ).then(res => {
                    this.listData.list = res.data.list || [];
                    this.listData.totalPage = res.data.totalPage || 1;
                }).catch(err => {
                    this.$message.error(`获取内容列表数据失败：${ err.message || '未知错误' }`);
                }).fin(() => {
                    this.loading = false;
                }).done();
            }
        }
    }

</script>



